.friendmain{
    width: 100%;
    height: 100vh;
    background-color: whitesmoke;
    padding: 50px 20px 20px;
}
.friendbox{
    width: 100%;
    height: 18vh;
    background-color: white;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    margin: 20px 0;
}

.friendbox .box{
    width: 50%;
    height: fit-content;
    padding: 20px 0;
}
.friendbox .box1{
    /* background-color: pink; */
    display: flex;
    justify-content: center;
    align-items: center;
}
.friendbox .box1 .img{
    width: 100px;
    height: 100px;
    /* background-color: aqua; */
    border-radius: 50%;
    border: 1px solid;
    overflow: hidden;
}
.friendbox .box1 .img img{
    width: 100%;
}
.friendbox .box1 .text{
    width: 190px;
    height: 80px;
    /* background-color: red; */
    padding: 10px 30px 0;
}
.friendbox .box1 .text p{
    font-size: 12px;
    padding-bottom: 0;
}
.friendbox .box2 div{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

.friendbox .box2 div button{
    margin: 0px 10px;
}