.gamesmain{
    width: 100%;
    /* background-color: red; */
    height: 100vh;
    overflow-y: scroll;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 40px 0 0 0;
}
.gamesmain::-webkit-scrollbar {
    display: none;
  }
.game-card {
    width: 200px;
    border: 1px solid #ddd;
    border-radius: 5px;
    overflow: hidden;
    background-color: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    margin: 10px;
  }
  
  .game-image {
    width: 100%;
    height: 150px;
    object-fit: cover;
  }
  
  .game-details {
    padding: 15px;
  }
  
  .game-name {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .game-rating {
    margin: 0;
    font-size: 16px;
    color: #333;
  }