.profilemain{
width: 100%;
height: 100vh;
background-color: red;
display: flex;
justify-content: center;
align-items: center;
}

.profilebox1{
 width: 20%;
 height: 100vh;
 background-color: yellow;   
}

.profilebox2{
    width: calc(100% - 20%);
    height: 100vh;
    background-color: blue
}

.profilebox1 .box{
    width: 100%;
    height: 290px;
    background-color: #4DAFF3;
    overflow-y: scroll;
}
.profilebox1 .box1{
    height: 360px;
    background-color: #4DAFF3;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-bottom: 1px solid;
    color: white;
}

.profilebox1 .box1 .profile{
    width: 160px;
    height: 160px;
    background-color: palegoldenrod;
    border-radius: 50%;
    margin-bottom: 20px;
    overflow: hidden;
    border: 1px solid;
}

.profilebox1 .box1 .profile img{
    width: 100%;
}

.profilebox1 .box2{
    padding: 20px 80px 20px;
    color: white;
}
.profilebox1 .box2 p img{
    border-radius: 50%;
}

.profilebox1 .box::-webkit-scrollbar {
    display: none;
  }
.profilebox2 .box1{
    width: 100%;
    height: 10vh;
    background-color: white;
    text-align: center;
    padding: 20px 0;
}
.profilebox2 .box1 span{
    position: absolute;
    right: 10px;
    top: 10px;
}

.profilebox2 .box2{
    width: 100%;
    height: calc(100vh - 10vh);
    background-color: whitesmoke;
    padding: 20px 20px;
    flex-wrap: wrap;
    display: flex;
    /* justify-content: center; */
    /* align-items: center; */
    overflow-y: scroll;
}

.profilebox2 .box2 .posts{
    width: 300px;
    height: 350px;
    background-color: white;
    border-radius: 10px;
    overflow: hidden;
    margin: 20px;
}

.profilebox2 .box2 .posts .box11{
    width: 100%;
    height: 40px;
    background-color:white;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    padding-left: 30px;
}
.profilebox2 .box2 .posts .box11 p{
    margin-top: 15px;
    margin-left: 15px;
}
.profilebox2 .box2 .posts .box11 .img{
    width: 20px;
    height: 20px;
    border-radius: 50%;
}
.profilebox2 .box2 .posts .box12{
    width: 100%;
    height: fit-content;
    background-color:white;
}
.profilebox2 .box2 .posts .box12 .pic img{
    width: 100%;
}